import { withFormik } from "formik";
import React from "react";
import styled from "styled-components";
import { PrimaryButton } from "../Common/components/Buttons/PrimaryButton";
import { Card } from "../Common/components/Card/Card";
import Input from "../Common/components/Input/Input";

const InputWrapper = styled.div`
  margin: 10px;
  display: flex;
  flex-direction: column;
`;

const LoginButton = styled(PrimaryButton)`
  margin-top: 20px;
  width: 100%;
`;

const Title = styled.h1`
  margin: 0;
  text-align: center;
`;

function ForgotPassword({ values, handleChange, handleSubmit }) {
  return (
    <Card width={400}>
      <Title>Forgot Password</Title>
      <InputWrapper>
        <Input
          label="Phone"
          type="text"
          name="phone_no"
          onChange={handleChange}
          value={values.phone_no}
          placeholder="Enter phone number"
        />
        <Input
          label="Password"
          type="password"
          name="password"
          value={values.password}
          onChange={handleChange}
          placeholder="Enter password"
        />
        <LoginButton onClick={handleSubmit}>Submit</LoginButton>
      </InputWrapper>
    </Card>
  );
}

const ForgotPasswordFormik = withFormik({
  mapPropsToValues: () => ({ phone_no: "", password: "" }),
  handleSubmit: (values, { props }) => {
    props.login(
      values.phone_no,
      values.password,
      (user) => {
        if (user.data[0].is_admin) {
          props.history.push("/admin/profiles");
        } else {
          props.history.push("/matches");
        }
      },
      (err) => {}
    );
  },
})(ForgotPassword);

export default ForgotPasswordFormik;
